.blue-underline {
  border-bottom: solid 5px #00b5d2;
  padding-bottom: 10px;
  margin-bottom: 10px;
  font-style: normal;
  font-size: 48px;
}

.join-our-team__paragraph {
  font-style: italic;
  font-size: 18px;
}

h2 {
  color: white;
  font-size: 48px;
  font-weight: 900;
}

img {
  max-width: 100%;
  height: auto;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

main {
  @include flex(column, nowrap, center);
  align-items: center;
}

a {
  text-decoration: none;
  &:visited {
    color: $text-main-color;
  }
}

p {
  font-size: 18px;
}

.container {
  @include flex(column, nowrap, center);
  max-width: 1200px;
  margin: auto;
}
