.mobile-nav-bar_icon {
  div {
    z-index: 4;
    width: 35px;
    height: 5px;
    background-color: #00b5d2;
    margin: 0 0 6px;
  }
}

.mobile-nav-bar {
  @include flex(column, wrap, flex-start);
  align-items: stretch;
  position: sticky;
  position: absolute;
  height: 100%;
  width: 100%;
  top: 90px;
  left: 0;
  z-index: 444;
  background-color: #00b6d2fb;

  li {
    width: 100%;
    color: white;
    list-style: none;
    padding: 30px;
    border-bottom: solid white 2px;
  }
}
