header {
  @include flex(row, wrap, space-between);
  z-index: 2;
  align-items: flex-start;
}

.header-container {
  z-index: 2;
  background: rgb(233, 180, 81) url(../img/jared-sluyter-342881.png) no-repeat;
  background-size: cover;
  padding: 30px;
  min-height: 660px;
  width: 100%;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.35);
}

.logo__container {
  @include flex(row, nowrap, flex-start);
  align-items: center;
}

.logo__title {
  margin: 0;
  font-weight: normal;
}

.logo__img {
  max-height: 30px;
}

.nav-bar {
  @include flex(row, nowrap, flex-end);
  list-style: none;

  a {
    padding: 7.5px 20px 0;

    &:first-child {
      padding-left: 0;
    }
  }

  li {
    padding-bottom: 40px;
    color: $text-main-color;
    border-bottom: solid #00b6d200 5px;

    &:first-child {
      padding-left: 0;
    }

    &:hover {
      color: white;
      border-bottom: solid #00b5d2 5px;
    }
  }
}

.we-can-help {
  padding-top: 8%;

  p {
    max-width: 450px;
    line-height: 30px;
    color: white;
    font-size: 18px;
  }
}
