.btn {
  display: inline-block;
  background-color: #e1aa6e;
  font-size: 20px;
  padding: 20px 0;
  border-bottom: 10px solid #ba8e5b;
  color: white;
  max-width: 286px;
  text-align: center;
  text-shadow: none;
  width: 100%;

  &:visited {
    color: white;
  }

  &:hover {
    background-color: #ba8e5b;
    border-bottom: 10px solid #e1aa6e;
  }
}

.btn-find-help {
  margin-top: 30px;
}

.btn_blue {
  background-color: #00b5d2;
  border-bottom-color: #008ea5;

  &:hover {
    background-color: #008ea5;
    border-bottom-color: #00b5d2;
  }
}
