.partners {
  @include flex(row, wrap, space-evenly);
  width: 100%;
  align-items: center;

  img {
    padding: 30px 15px;
  }
}

.support-for-people {
  @include flex(column, nowrap, center);
  align-items: center;
  background-color: #00b5d2;
  width: 100%;
  color: white;
  text-align: center;
  padding: 30px;

  h2 {
    margin: 100px 0 15px;
  }

  p {
    margin-bottom: 80px;
  }
}

.services-help {
  @include flex(column, nowrap, space-evenly);
  align-items: center;
  text-align: center;
  color: $text-main-color;
  padding: 70px 35px;

  h3 {
    color: $text-main-color;
    font-size: 24px;
    margin: 0;
  }

  p {
    width: 75%;
    padding: 30px 0;
  }
}

.in-home-support {
  @include flex(row, wrap, center);
  padding: 110px 30px 0 30px;
  position: relative;
  align-items: center;
  background: #00b5d2;
  width: 100%;
  z-index: 1;

  img {
    position: relative;
    z-index: 2;
    top: 50px;
  }
}

.white-blank {
  height: 110px;
}

.join-our-team {
  @include flex(row, nowrap, flex-start);

  @media screen and (max-width: 768px) {
    flex-direction: column;

    img {
      display: none;
    }
  }

  h2 {
    margin: 0;
  }

  article {
    @include flex(column, wrap, space-evenly);
    background-color: #367ab7;
    align-items: flex-start;
    padding: 30px 30px;
    color: white;
  }
}
