footer {
  @include flex(column, wrap, center);
  align-items: center;
  margin-top: 60px;
  border-top: #cccccc 3px solid;
  color: $text-main-color;
}

.footer-links {
  @include flex(row, wrap, space-between);
  align-items: center;
  list-style: none;
  font-weight: bold;

  a {
    padding: 30px;
  }

  li {
    &:hover {
      color: #00b5d2;
    }
  }
}

.squares_container {
  @include flex(row, nowrap, center);
}

.squares {
  width: 13px;
  height: 13px;
  margin: 0 20px;
  background-color: #eeeeee;
}

.footer-adress {
  @include flex(column, wrap, center);
  align-items: center;
  line-height: 40px;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  padding: 30px;
  opacity: 0.6;
  text-align: center;
}

.footer-phone {
  color: $text-main-color;
}

.footer-email {
  color: #d1720c;
  font-size: 16px;
}

.footer-copyright {
  opacity: 0.6;
  font-size: 12px;
  letter-spacing: 0.6px;
  border-top: solid 1px #eeeeee;
  width: 74%;
  text-align: center;
  padding: 30px 0 50px;
}
